import { EmployeeRole } from './../../models/employeeRole';
import { Component } from '@angular/core';
import { Employee } from './../../models/employee';
import { EmployeeService } from './../../services/employee.Service'

@Component({
    selector: 'employee-form',
    template: require('./employee.template.html'),
    styles:[require('./employee.style.css')],
    providers:[EmployeeService]
})

export class EmployeeFormComponent {
    public newEmployee;
    public employeeRoles = [];
    constructor(private _emplloyeeService: EmployeeService) { 

    }


    addEmployee(): void {
        console.log("Add Employee is Cliecked TS");
        console.log(this.newEmployee);
        this._emplloyeeService.addEmployee(this.newEmployee).subscribe(res => {
            console.log(res);
        
            
            console.log(this.newEmployee);

            if (this.newEmployee.EmployeeRole != null ||  this.newEmployee.Address != null || 
                    this.newEmployee.ContactNumber != null || 
                    this.newEmployee.FullName != null || this.newEmployee.Email != null ||
                    this.newEmployee.CNIC != null ) {
                    
                        $("#snackbar").html("New Employee Create");
                        this.showToast();   

                } else {
                         
                         $("#snackbar").html("Please Insert Empty Fields");
                         this.showToast();               
                }
            
        });
    }
    showToast() {
        // Get the snackbar DIV
        var x = document.getElementById("Message")

        // Add the "show" class to DIV
        x.className = "alert alert-warning";

        // After 3 seconds, remove the show class from DIV
        
        setTimeout(function () { x.className = x.className.replace("alert alert-warning", ""); }, 3000);
    }

    onChange(_id:string):void
    {
        console.log("CHange is Clicked"+_id);
        this.newEmployee.EmployeeRole=_id;
    }

    ngOnInit(): void {
        this.newEmployee = new Employee();
        console.log("NG ON INIT IS CALLED");
        this.employeeRoles = this._emplloyeeService.getEmployeeRoles();
      }

}


